
// see documentation here: https://www.npmjs.com/package/use-file-picker#install

import { useFilePicker } from 'use-file-picker';

function SelectCVdataFile() {

  const { openFilePicker, filesContent, loading } = useFilePicker({
    accept: '.txt',
    multiple : false
  });

  if (loading) {
    return <div>Loading...</div>;
  }


  if (filesContent && filesContent.length !== 0)
    {
      let fileContent = filesContent[0].content;      
      const obj = JSON.parse(fileContent);   
    }
 
  return (
    <div className="cv_content">
      <div className="cv_header_contact_info"></div>
      <div className="cv_header"></div>
      <div className="cv_right">
        <button onClick={() => openFilePicker()}>Vlg fil</button>
        <br />     
      </div>
    </div>
  );

}

export default SelectCVdataFile;