export class Profile {
    sectionname: string
    profiletext: string

    public constructor(sectionname: string, profiletext: string) {
        this.sectionname = sectionname;
        this.profiletext = profiletext;
    }
}


export class Motivation {
    sectionname: string
    motivationtext: string

    public constructor(sectionname: string, motivationtext: string) {
        this.sectionname = sectionname;
        this.motivationtext = motivationtext;
    }
}

export class ContactInfoEntry {
    description: string
    icon: string
    type: string

    public constructor(description: string, icon: string, type: string) {
        this.description = description;
        this.icon = icon
        this.type = type
    }
}

export class ContactInfo {
    cvname: string
    entries: ContactInfoEntry[];
    public constructor(cvname: string, entries: ContactInfoEntry[]) {
        this.cvname = cvname;
        this.entries = entries;
    }
}



export class WorkingExperienceEntry {

    title: string
    fromdate: string
    todate: string
    usedskills: string[]
    descriptions: string[]
    achievements: string[]
    icon: string


    public constructor(title: string, fromdate: string, todate: string, usedskills: string[], descriptions: string[], achievements: string[], icon: string) {
        this.title = title
        this.fromdate = fromdate
        this.todate = todate
        this.usedskills = usedskills
        this.descriptions = descriptions;
        this.achievements = achievements;
        this.icon = icon
    }
}

export class WorkingExperience {
    sectionName: string
    achivementstitle: string
    entries: WorkingExperienceEntry[]

    public constructor(sectionName: string, achivementstitle: string, entries: WorkingExperienceEntry[]) {
        this.sectionName = sectionName;
        this.achivementstitle = achivementstitle
        this.entries = entries
    }
}

export class SkillEntry {
    description: string
    stars: number

    public constructor(description: string, stars: number) {

        this.description = description;
        this.stars = stars;
    }
}
 
export class Skills {
    sectionName: string
    entries: SkillEntry[]

    public constructor(sectionName: string, entries: SkillEntry[]) {

        this.sectionName = sectionName;
        this.entries = entries;
    }
}

export class Educations {
    sectionName: string
    entries : EducationEntry[]

    public constructor(sectionName: string,  entries : EducationEntry[]) {

        this.sectionName = sectionName;
        this.entries = entries;
    }
}
 
export class EducationEntry {
    title: string
    todate: string
    location: string 

    public constructor(title: string, todate: string, location: string) {

        this.title = title;
        this.todate = todate;
        this.location = location
    }
} 

export class LanguageEntry {
    language: string
    level: string
    
    public constructor(language: string, level: string) {

        this.language = language;
        this.level = level;
      
    }
}

export class Languages {
    sectionName: string
    entries : LanguageEntry[]

    public constructor(sectionName: string,  entries : LanguageEntry[]) {

        this.sectionName = sectionName;
        this.entries = entries;
    }
}

export class Sparetime {
    sectionName: string
    entries : SparetimeEntry[]

    public constructor(sectionName: string,  entries : SparetimeEntry[]) {

        this.sectionName = sectionName;
        this.entries = entries;
    }
}



export class SparetimeEntry {
    interest: string

    public constructor(interest: string) {

        this.interest = interest;       
    }
}

export class CVData {
    Sparetime  : Sparetime;
    Skills  : Skills;
    WorkingExperience :WorkingExperience;
    Languages  : Languages;
    Educations  : Educations;
    Motivation  :Motivation;
    ContactInfo  : ContactInfo
    Profile  : Profile;

    public constructor(
        ContactInfo  : ContactInfo, 
        Profile  : Profile, 
        Motivation  :Motivation,
        WorkingExperience :WorkingExperience,
        Skills  : Skills,
        Educations  : Educations,
        Languages  : Languages,
        Sparetime  : Sparetime    
    ) {

        this.ContactInfo = ContactInfo;     
        this.Profile = Profile;  
        this.Motivation = Motivation;
        this.WorkingExperience = WorkingExperience;
        this.Skills = Skills;
        this.Educations = Educations;
        this.Languages = Languages;
        this.Sparetime = Sparetime;
    }

  } 
