
import Motivation from './Motivation';
import Profile from './Profile';
import ContactInfo from './ContactInfo';
import Skills from './Skills';
import Education from './Education';
import Languages from './Languages';
import WorkingHistory from './WorkingHistory';
import Sparetime from './Sparetime';
import { Routes, Route, useLocation } from 'react-router-dom';
import SaveCVDataToFile from './SaveCVDataToFile';
import SelectCVdataFile from './SelectCVdataFile';
import OpenCVdataFile from './OpenCVdataFile'
import ConvertFromUrlToPdf from './ConvertFromUrlToPdf' 
import { useState, createContext } from 'react';
import { defaultCVData , setCurrentCVDataFileName, CurrentCVDataFileName} from './GlobalCVData';
import { CVData } from './ClassesCVData';


var _ = require('lodash');

export const MyContext = createContext(defaultCVData,);

function App() {



  const location = useLocation(); 
  console.log(location);


  let base_url = process.env.REACT_APP_BASE_URL as string 
  console.log(base_url);


  let [cvdataToUse, setCVdataToUse] = useState(defaultCVData); 

  function setNewCVData(newCVData: CVData, cvDataFileName : string) {
    setCVdataToUse(newCVData);
    setCurrentCVDataFileName(cvDataFileName);
  }

  return ( 
    <div>      
      <MyContext.Provider value={cvdataToUse} >
        <div className="App bg">
          <div className="cv_content">
            <div className="cv_header_contact_info"><ContactInfo /></div>
            <div className="cv_header"><Motivation /></div>
            <div className="cv_right">
              <Profile />
              <WorkingHistory /> 
            </div>
            <div className="cv_left">
              <Skills />
              <Education />
              <Languages />
              <Sparetime />
              <ConvertFromUrlToPdf CurrentCVDataFileName={CurrentCVDataFileName} />
              {/* <HtmlToPdfDocRaptor /> */}
            </div>
          </div>
        </div>
      </MyContext.Provider>  

      <Routes >
        {/* <Route path={process.env.REACT_APP_BASE_URL}></Route> */}
          <Route path={base_url +'savefile'} element={<SaveCVDataToFile></SaveCVDataToFile>}></Route>
          <Route path={base_url +'openfile'} element={<SelectCVdataFile></SelectCVdataFile>}></Route>
          <Route path={base_url} element={
            <OpenCVdataFile
              SetCVdata={(newCVData: CVData, cvDataFilename : string) => {
                setNewCVData(newCVData, cvDataFilename)
              }
              } 
            >
            </OpenCVdataFile>
          }>
          </Route>       
      </Routes>

    </div>
  );
}

export default App;
