// import   {currenrCVData}  from './GlobalCVData';

import { MyContext } from './App';
import { CVData } from './ClassesCVData';
import   { currenrCVData}  from './GlobalCVData';
import {useState, useContext} from 'react';

function Sparetime() {

    const cvdata  = useContext(MyContext);
   

    return (
        <div>
            <p className="section_title">{cvdata.Sparetime.sectionName}</p>

            {cvdata.Sparetime.entries.map((SparetimeEntry) => (

                <div>
                    <p style={{  lineHeight: '1.2'}} className='sparetime_entry_title'>{SparetimeEntry.interest}</p>                                                  
                </div>
            ))}
            <hr className="section_ruler"></hr>
        </div>
    );
} 

export default Sparetime;