import   {currenrCVData}  from './GlobalCVData';

function Motivation() {
  

  return (
    <div>
      <p className="section_title">{currenrCVData.Motivation.sectionname}</p>
      <p style={{ lineHeight: '1.5' }}>{currenrCVData.Motivation.motivationtext}</p>
    </div>
  );
}

export default Motivation;